$primary-color: #6a4e35;   // æŸ”å’Œä¸”å…·å¯è®€æ€§çš„èƒŒæ™¯é¡è‰²
$hover-color: #8c674c;     // æ‡¸åœæ™‚çš„é¡è‰²
$font-family: 'Arial', sans-serif;  // æ›´å…·å¯è®€æ€§çš„å­—é«”
$button-padding: 1.5rem 2.5rem;    // èª¿æ•´æŒ‰éˆ•çš„é–“è·ï¼Œè®“å®ƒæ›´é©åˆè§¸æ§æ“ä½œ
$button-font-size: 1.2rem;         // èª¿æ•´æŒ‰éˆ•å­—é«”å¤§å°
