/* åŸºæœ¬å®¹å™¨æ¨£å¼ */
.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #f4f4f4;   // æ›´æŸ”å’Œçš„èƒŒæ™¯é¡è‰²ï¼Œæ¸›å°‘çœ¼ç–²å‹
  color: #333;
  text-align: center;
  font-family: $font-family;
}

/* æŒ‰éˆ•å®¹å™¨æ¨£å¼ */
.buttonContainer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  justify-content: center;
}

/* æŒ‰éˆ•åŸºæœ¬æ¨£å¼ */
.button {
  padding: $button-padding;
  font-size: $button-font-size;
  background-color: $primary-color;
  color: white;
  border: none;
  border-radius: $button-border-radius;
  cursor: pointer;
  box-shadow: $box-shadow;
  transition: background-color 0.3s ease, transform 0.3s ease;
  line-height: 1.5;
  text-align: center;
  margin-top: 1rem;

  &:hover {
    background-color: $hover-color;
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.98);  // å¢åŠ é»æ“Šæ™‚çš„è¼•å¾®å£“ç¸®æ•ˆæœ
  }
}

/* å±…ä¸­å…§å®¹çš„æ¨£å¼ */
.centered-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* å…·é«”æŒ‰éˆ•æ¨£å¼ */
.foundButton,
.endButton {
  @extend .button;
}

/* åª’é«”æŸ¥è©¢ï¼šé©æ‡‰å¹³æ¿è¨­å‚™ */
@media (max-width: 768px) {
  .button {
    font-size: 1.8rem;
    padding: 1.5rem 2.5rem;
    border-radius: 1rem;
  }

  .centered-content {
