/* å…¨å±€æ¨£å¼è®Šæ•¸ */
$primary-color: #8b5a2b;
$hover-color: #a07246;
$button-padding: 12px 24px;
$button-radius: 10px;
$button-transition: background-color 0.3s ease, transform 0.3s ease;
$box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
$text-shadow: 0.2vw 0.2vw 0.4vw rgba(0, 0, 0, 0.7);

/* ä¸»å®¹å™¨æ¨£å¼ */
.container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background: none;
    color: #fff;
    text-align: center;
    font-family: 'Comic Sans MS', cursive, sans-serif;
    transition: background-color 0.5s ease-in-out;
}

/* æŒ‰éˆ•å®¹å™¨ */
.buttonContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 20px;  /* æé«˜æŒ‰éˆ•ä¹‹é–“çš„é–“è· */
}

/* æŒ‰éˆ•æ¨£å¼ */
.foundButton,
.endButton {
    padding: 1.5vh 3vh;
    font-size: 1.5vw;
    background-color: $primary-color;
    color: white;
    border: none;
    border-radius: 0.8vw;
    cursor: pointer;
    box-shadow: $box-shadow;
    transition: $button-transition;
    line-height: 1.5;
    text-align: center;
    text-shadow: $text-shadow;

    &:hover {
        background-color: $hover-color;
        transform: scale(1.1);
    }
}

/* èª¿æ•´æŒ‰éˆ•å¤§å°èˆ‡æ¨£å¼é©æ‡‰ä¸åŒè£ç½® */
@media (max-width: 768px) {
    .foundButton,
    .endButton {
        font-size: 3vw;
        padding: 3vh 4vh;
        border-radius: 1.2vw;
    }
}

@media (max-width: 480px) {
    .foundButton,
    .endButton {
        font-size: 4vw;
        padding: 4vh 6vh;
        border-radius: 2vw;
    }
}

/* èƒŒæ™¯éš±è—æ¨£å¼ */
.hideBackground {
    background-image: none !important;
    background-color: rgba(0, 0, 0, 0.6); /* æä¾›æ·±è‰²èƒŒæ™¯ä»¥å¼·èª¿å…§å®¹ */
    transition: background-color 0.5s ease-in-out;
}

/* å‹•æ…‹æŒ‰éˆ•æ•ˆæœï¼šåœ¨æ‰¾åˆ°ç›®æ¨™æ™‚é¡¯ç¤º */
@keyframes pulse {
    0% {
        transform: scale(1);
        box-shadow: $box-shadow;
    }
    50% {
        transform: scale(1.05);
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
    }
    100% {
        transform: scale(1);
        box-shadow: $box-shadow;
    }
}

/* åœ¨æ‰¾åˆ°ç›®æ¨™æ™‚ï¼Œè®“æŒ‰éˆ•é€²è¡Œè„ˆå‹•å‹•ç•« */
.targetFound .foundButton,
.targetFound .endButton {
    animation: pulse 1.5s infinite;
}

/* ä¿¡æ¯æç¤ºæ¨£å¼ */
.infoText {
    font-size: 2vw;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 1.5vh 2vh;
    border-radius: 0.8vw;
    text-align: center;
    margin-top: 2vh;
    box-shadow: $box-shadow;
}

/* é‡å°è¼ƒå°è¢å¹•çš„æç¤ºæ¨£å¼ */
@media (max-width: 768px) {
    .infoText {
        font-size: 3vw;
        padding: 2vh 3vh;
    }
}

@media (max-width: 480px) {
    .infoText {
        font-size: 4vw;
        padding: 2.5vh 4vh;
    }
}
