/* å…¨å±€è®Šæ•¸ */
$primary-color: #8b5a2b;
$hover-color: #a07246;
$font-family: "Comic Sans MS", cursive, sans-serif;
$button-padding: 1.3vh 2.6vh;
$button-font-size: 1.3vw;
$button-border-radius: 0.5vw;
$box-shadow: 0 0.4vw 0.8vw rgba(0, 0, 0, 0.2);

/* åŸºæœ¬å®¹å™¨æ¨£å¼ */
.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: none;
  color: #fff;
  text-align: center;
  font-family: $font-family;
}

/* æŒ‰éˆ•å®¹å™¨æ¨£å¼ */
.buttonContainer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  justify-content: center;
}

/* æŒ‰éˆ•åŸºæœ¬æ¨£å¼ */
.button {
  padding: $button-padding;
  font-size: $button-font-size;
  background-color: $primary-color;
  color: white;
  border: none;
  border-radius: $button-border-radius;
  cursor: pointer;
  box-shadow: $box-shadow;
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  line-height: 1.5;
  text-align: center;
  margin-top: 0.5rem;

  &:hover {
    background-color: $hover-color;
    transform: scale(1.05);
    box-shadow: 0 0.6vw 1.2vw rgba(0, 0, 0, 0.3);  // æå‡æŒ‰éˆ•æ‡¸åœæ™‚çš„é™°å½±æ•ˆæœ
  }

  &:active {
    transform: scale(0.98);  // æŒ‰å£“æ™‚çš„åé¥‹æ•ˆæœ
    box-shadow: none;  // ç§»é™¤æŒ‰å£“æ™‚çš„é™°å½±
  }
}

.centered-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* å…·é«”æŒ‰éˆ•æ¨£å¼ */
.foundButton,
.endButton {
  @extend .button;
}

.endButton {
  background-color: darkred;
  &:hover {
    background-color: firebrick;
  }
}

/* QRæƒææŒ‰éˆ•æ¨£å¼ */
.qr-scan-button {
  padding: 1vh 2vw;
  font-size: 1.5vw;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 1vw;
  cursor: pointer;
  box-shadow: 0 0.3vw 0.6vw rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;

  &:hover {
    background-color: #0056b3;
    transform: scale(1.05);
    box-shadow: 0 0.6vw 1.2vw rgba(0, 0, 0, 0.3);
  }

  &:active {
    transform: scale(0.95);
    box-shadow: none;
  }
}

/* åª’é«”æŸ¥è©¢ï¼šé©æ‡‰å¹³æ¿è¨­å‚™ */
@media (max-width: 768px) {
  .button {
    font-size: 2.6vw;
    padding: 2vw 4vw;
    border-radius: 1vw;
  }
}

/* åª’é«”æŸ¥è©¢ï¼šé©æ‡‰æ‰‹æ©Ÿè¨­å‚™ */
@media (max-width: 480px) {
  .button {
    font-size: 3vw;
    padding: 2.5vw 5vw;
    border-radius: 1.5vw;
  }

  .qr-scan-button {
    font-size: 3.5vw;
    padding: 3vw 6vw;
  }
}

/* éš±è—èƒŒæ™¯æ¨£å¼ */
.hideBackground {
  background-image: none !important;
}

