:root{--font-family: "Comic Sans MS", "Comic Sans", "Chocolate Classical Sans";--bg-gradient-start: #7892c2;--bg-gradient-end: #476e9e;--border-color: #4e6096;--shadow-color: #9fb4f2;--text-color: #ffffff;--highlight-color: #3da35d;--highlight-hover-color: #329547;--border-radius: 10px;--font-size-lg: 18px;--font-size-md: 16px;--font-size-sm: 14px;--font-size-xs: 12px}*{margin:0;padding:0;box-sizing:border-box;font-family:var(--font-family)}html,body{width:100vw;height:100vh;overflow:hidden;display:flex;justify-content:center;flex-direction:column;background-image:url(./assets/picture/BG/èƒŒæ™¯-æ´»å‹•ä¸­å¿ƒ.png);background-size:cover;background-repeat:no-repeat;background-position:center}.container,.centered-container{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center;width:100%;height:100%}.centered-image{width:100%;height:auto;object-fit:cover;border:2% solid #fff;border-radius:2%;box-shadow:var(--box-shadow)}.button,.startButton,.intro-container button{background-color:var(--highlight-color);color:var(--text-color);border:none;border-radius:var(--border-radius);cursor:pointer;transition:background-color .3s ease,transform .3s ease,box-shadow .3s ease}.button:hover,.startButton:hover,.intro-container button:hover{background-color:var(--highlight-hover-color);transform:scale(1.05);box-shadow:0 .5em .5em -0.4em var(--shadow-color)}@media screen and (max-width: 1024px),(max-width: 768px),(max-width: 480px),(orientation: portrait){.startButton,.intro-container{width:50vw;height:20vh;font-size:var(--font-size-sm)}.intro-container button{font-size:var(--font-size-sm)}}