.container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background: none;
    color: #fff;
    text-align: center;
    font-family: 'Comic Sans MS', cursive, sans-serif;
    transition: background 0.5s ease; /* èƒŒæ™¯çš„å¹³æ»‘éæ¸¡æ•ˆæœ */
}

/* æŒ‰éˆ•å®¹å™¨æ¨£å¼ */
.buttonContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 2vh; /* å¢åŠ æŒ‰éˆ•é–“çš„é–“è· */
}

/* æŒ‰éˆ•åŸºæœ¬æ¨£å¼ */
.foundButton,
.endButton {
    padding: 1.5vh 3vh;
    font-size: 1.4vw;
    background-color: #8b5a2b;
    color: white;
    border: none;
    border-radius: 0.7vw;
    cursor: pointer;
    box-shadow: 0 0.5vw 1vw rgba(0, 0, 0, 0.3);
    transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
    line-height: 1.5;
    text-align: center;
    outline: none;
}

/* æŒ‰éˆ•æ‡¸åœæ™‚çš„äº’å‹•æ•ˆæœ */
.foundButton:hover,
.endButton:hover {
    background-color: #a07246;
    transform: scale(1.08);
    box-shadow: 0 0.6vw 1.2vw rgba(0, 0, 0, 0.4);
}

/* æ‰‹æ©Ÿå’Œå¹³æ¿éŸ¿æ‡‰å¼èª¿æ•´ */
@media (max-width: 768px) {
    .foundButton,
    .endButton {
        font-size: 3vw;
        padding: 2.5vh 5vh;
        border-radius: 1.2vw;
    }
}

@media (max-width: 480px) {
    .foundButton,
    .endButton {
        font-size: 4vw;
        padding: 3vh 6vh;
        border-radius: 1.5vw;
    }
}

